import React from 'react';
/* import './index.less';
 */
interface Props {
    checked: boolean;
    onChange: (e: React.ChangeEvent) => void;
}

const Switch: React.FunctionComponent<Props> = (props) => {
    return (
        <div>
            <h2>Switch开关：</h2>
            <div className={'self-ui-switch'} style={{ display: 'inline-flex', verticalAlign: 'top' }}>
                <input
                    id={'switch-input'}
                    className={'self-ui-switch-input'}
                    type="checkbox"
                    onChange={props.onChange}
                    checked={props.checked}
                    /* style={{height:'0',width:'0',display:'none'}} */
                />
                <label
                    htmlFor="switch-input"
                    /* style={{
                    position: 'relative',
                    width: '100px',
                    height: '50px',
                    backgroundColor: 'gray',
                    borderRadius: '100px',
                    cursor: 'pointer',
                }} */
                >
                    <span
                        className={'self-ui-switch-button'}
                        /* style={{
                        position: 'absolute',
                        height: '46px',
                        width: '46px',
                        top: '50%',
                        left: '2px',
                        borderRadius: '46px',
                        transform:'translateX(-50%)',
                        backgroundColor:'white'
                    }} */
                    />
                </label>
            </div>
        </div>
    );
};

export default Switch;
