import React, { Component } from 'react';

class index extends Component<any> {
    render() {
        return (
            <div style={{ marginLeft: '12px' }}>
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div className="para">
                        <h2>checkbox多选</h2>
                    </div>
                    <div style={{ height: '1em', fontSize: '1em' }}>{this.props.checkbox}</div>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div className="para">
                        <h2>Select多选框</h2>
                    </div>
                    <div style={{ height: '1em', fontSize: '1em' }}>{this.props.select}</div>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div className="para">
                        <h2>input输入后可以清除</h2>
                    </div>
                    <div style={{ height: '2em', fontSize: '1em' }}>{this.props.input}</div>
                </div>
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div className="para">
                        <h2>switch开关</h2>
                    </div>
                    <div style={{ height: '2em', fontSize: '1em' }}>{this.props.switch}</div>
                </div>
            </div>
        );
    }
}

export default index;
