import React, { Component } from 'react';

class index extends Component<any> {
    state = {
        checkedValue: [],
    };

    handleCheckbox = (event: any) => {
        const { target } = event;

        const { checkedValue } = this.state;
        var index = checkedValue.indexOf(target.value as never);

        if (index === -1) {
            //该元素尚未选中，添加至checkedValue
            const newCheckedValue = [target.value, ...checkedValue];
            this.setState({ checkedValue: newCheckedValue });

            console.log('checkbox\t' + newCheckedValue);
        } else {
            //该元素已选中，从checkedValue移除

            checkedValue.splice(index, 1);
            const newCheckedValue = checkedValue;
            this.setState({ checkedValue: newCheckedValue });

            console.log('checkbox\t' + newCheckedValue);
        }

        this.props.handleCheckbox(this.state.checkedValue);
    };

    render() {
        return (
            <div>
                <h2>checkbox多选</h2>
                <form name="checkboxForm">
                    <p>
                        <input type="checkbox" value="数学" onChange={this.handleCheckbox.bind(this)} />
                        数学
                    </p>
                    <p>
                        <input type="checkbox" value="语文" onChange={this.handleCheckbox.bind(this)} />
                        语文
                    </p>
                    <p>
                        <input type="checkbox" value="英语" onChange={this.handleCheckbox.bind(this)} />
                        英语
                    </p>
                </form>
            </div>
        );
    }
}

export default index;
