export default class App extends Component {
    /*     const [checked:any,setChecked:any] = useState(false);
     */ state = {
        checkbox: [],
        select: '',
        slide: 0,
        input: '',
        switch: false,
        visible: false,
    };

    //点击提交按钮
    handleCheckbox = (value: string[]) => {
        this.setState({ checkbox: value });
        console.log('App\n', this.state.checkbox);
    };

    handleSelect = (value: string) => {
        this.setState({ select: value });
        console.log('App\n', this.state.select);
    };

    handleInput = (value: string) => {
        this.setState({ input: value });
        console.log('Input'+this.state.input);
    };

    render() {
        return (
            <div className="app">
                <div className="components">
                    {' '}
                    <Checkbox handleCheckbox={this.handleCheckbox}></Checkbox>
                    <Select handleSelect={this.handleSelect}></Select>
                    <Slide></Slide>
                    <Input handleInput={this.handleInput}></Input>
                    <Switch
                        checked={this.state.switch}
                        onChange={() => {
                            this.setState({ checked: !this.state.switch });
                            console.log(this.state.switch);
                        }}
                    ></Switch>
                    <button
                        type="submit"
                        value="提交"
                        style={{ marginTop: '10px' }}
                        onClick={() => {
                            message.success('提交成功，即将更新右侧');
                            this.setState({ visible: true });
                        }}
                    >
                        提交
                    </button>
                </div>
                {/* <div
                    style={{
                        width: '0px',
                        height: '800px',
                        borderRight: '1px dashed black',
                    }}
                ></div> */}
                <div className="results">
                    {this.state.visible ? (
                        <Results
                            checkbox={this.state.checkbox}
                            select={this.state.select}
                            input={this.state.input}
                            switch={this.state.switch}
                        ></Results>
                    ) : (
                        <div />
                    )}
                </div>
            </div>
        );
    }
}

ReactDOM.render(<App />, document.getElementById('root'));
