import React, { Component } from 'react';

//Input的子组件
class index extends Component<any> {
    handleClear = () => {
        console.log('handleClear called.');
        this.props.clear();
    };
    render() {
        return (
            <div
                onClick={this.handleClear.bind(this)}
                style={{cursor: 'pointer'}}
            >
                x
            </div>
        );
    }
}

export default index;
